import { useState, useEffect } from "react";
import { jwtDecode } from "jwt-decode";

const getOrCreateDeviceId = () => {
  let deviceId = localStorage.getItem("deviceId");
  if (!deviceId) {
    deviceId = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c === "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
    localStorage.setItem("deviceId", deviceId);
  }
  return deviceId;
};

export const usePremiumVerification = (premiumApiKey) => {
  const [isPremium, setIsPremium] = useState(false);
  const [sessionApiKey, setSessionApiKey] = useState(null);
  const [trialExpiresIn, setTrialExpiresIn] = useState("");
  const [modalInfo, setModalInfo] = useState({
    isOpen: false,
    title: "",
    message: "",
  });
  const COMMON_TRIAL_KEY = "TRIAL-KEY-7d1f2e9b";
  useEffect(() => {
    if (!sessionApiKey) {
      setTrialExpiresIn("");
      return;
    }
    const decodedToken = jwtDecode(sessionApiKey);
    const expirationTime = decodedToken.exp * 1000;
    const intervalId = setInterval(() => {
      const now = new Date().getTime();
      const distance = expirationTime - now;
      if (distance < 0) {
        clearInterval(intervalId);
        setTrialExpiresIn("Trial Expired");
        setIsPremium(false);
        return;
      }
      const hours = Math.floor(
        (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((distance % (1000 * 60)) / 1000);
      setTrialExpiresIn(`Trial expires in: ${hours}h ${minutes}m ${seconds}s`);
    }, 1000);
    return () => clearInterval(intervalId);
  }, [sessionApiKey]);

  useEffect(() => {
    const verifyKey = async (keyToVerify) => {
      if (!keyToVerify) {
        setIsPremium(false);
        return;
      }
      if (keyToVerify === COMMON_TRIAL_KEY) {
        if (sessionApiKey) return;
        try {
          const deviceId = getOrCreateDeviceId();
          const response = await fetch(
            "https://text-editor-verification-api.vercel.app/api/activate-trial",{method: "POST",headers: { "Content-Type": "application/json" },body: JSON.stringify({ deviceId }),});
          const data = await response.json();
          if (response.ok && data.sessionKey) {
            setSessionApiKey(data.sessionKey);
            setIsPremium(true);
          } else {
            setIsPremium(false);
            setModalInfo({isOpen: true,title: "Trial Activation Failed",message: data.message ||"Your free trial for this device has expired. Please purchase a premium key to continue.",});
          }
        } catch (error) {
          setIsPremium(false);
          setModalInfo({ isOpen: true, title: "Connection Error", message:"Could not connect to the server to activate the trial. Please check your internet connection and try again.",});
        }
        return;
      }
      try {
        const response = await fetch(
          "https://text-editor-verification-api.vercel.app/api/verify-key",
          {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ apiKey: keyToVerify }),
          }
        );
        const data = await response.json();
        if (response.ok && data.isValid) {
          setIsPremium(true);
          setSessionApiKey(null);
          setTrialExpiresIn("");
        } else {
          setIsPremium(false);
        }
      } catch (error) {
        setIsPremium(false);
      }
    };

    verifyKey(premiumApiKey);
  }, [premiumApiKey, sessionApiKey]);
  return {
    isPremium,
    trialExpiresIn,
    modalInfo,
    closeModal: () => setModalInfo({ isOpen: false, title: "", message: "" }),
  };
};