import { useEffect, useRef, useState } from "react";
import PropTypes from "prop-types";
import {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  Code,
  Link,
  Image,
  List,
  ListOrdered,
  Quote,
  AlignLeft,
  AlignCenter,
  AlignRight,
  Indent,
  Outdent,
  Table,
  Pilcrow,
  Columns,
  Eraser,
  Undo,
  Redo,
  Minus,
  Type,
  ArrowDownUp,
  FileUp,
  Superscript,
  Subscript,
  Video,
  Sparkles,
} from "lucide-react";
import ModernToolbarButton from "./components/ModernToolbarButton";
import ModernDropdown from "./components/ModernDropdown";
import ModernColorPicker from "./components/ModernColorPicker";
import ModernEmojiPicker from "./components/ModernEmojiPicker";
import ModernToolBarStyle from "../../../../styles/theme/modern/ModernToolBar.style";

const FONT_FACES = [
  "Arial",
  "Georgia",
  "Helvetica",
  "Tahoma",
  "Times New Roman",
  "Verdana",
  "Courier New",
];
const HEADINGS = {
  "Heading 1": "<h1>",
  "Heading 2": "<h2>",
  "Heading 3": "<h3>",
  "Heading 4": "<h4>",
  "Heading 5": "<h5>",
  Paragraph: "<p>",
};
const LINE_HEIGHTS = {
  Single: "1",
  1.15: "1.15",
  1.5: "1.5",
  Double: "2",
};
const FONT_SIZES = { Small: "2", Normal: "3", Large: "5", Huge: "6" };
const FONT_SIZES_MAP_REVERSE = {
  "13px": "Small",
  "16px": "Normal",
  "24px": "Large",
  "32px": "Huge",
};
const HEADINGS_MAP_REVERSE = {
  h1: "Heading 1",
  h2: "Heading 2",
  h3: "Heading 3",
  h4: "Heading 4",
  h5: "Heading 5",
  p: "Paragraph",
};

const columnTableStyle ="width: 100%; border-collapse: collapse; table-layout: fixed;  margin: 0;  padding: 0;";
const columnCellStyle = "padding: 0; vertical-align: top;";
const responsiveCss = `
  <style>
    @media screen and (max-width: 600px) {
      .responsive-cell {
        display: block !important;
        width: 100% !important;
        box-sizing: border-box !important;
      }
    }
  </style>
`;
const COLUMN_LAYOUTS = {
  "2 Columns (1:1)": `
    ${responsiveCss}
    <table class="responsive-table" style="${columnTableStyle}">
      <tbody>
        <tr>
          <td class="responsive-cell" style="${columnCellStyle} width: 50%;">
            <p>Column 1</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 50%;">
            <p>Column 2</p>
          </td>
        </tr>
      </tbody>
    </table>`,

  "3 Columns (1:1:1)": `
    ${responsiveCss}
    <table class="responsive-table" style="${columnTableStyle}">
      <tbody>
        <tr>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C1</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C2</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C3</p>
          </td>
        </tr>
      </tbody>
    </table>`,

  "2 Columns (1:2)": `
    ${responsiveCss}
    <table class="responsive-table" style="${columnTableStyle}">
      <tbody>
        <tr>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C1</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 66.66%;">
            <p>C2</p>
          </td>
        </tr>
      </tbody>
    </table>`,

  "2 Columns (2:1)": `
    ${responsiveCss}
    <table class="responsive-table" style="${columnTableStyle}">
      <tbody>
        <tr>
          <td class="responsive-cell" style="${columnCellStyle} width: 66.66%;">
            <p>C1</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C2</p>
          </td>
        </tr>
      </tbody>
    </table>`,
};

const ModernToolbar = ({
  currentStyles,
  applyStyle,
  applyLineHeight,
  activeStyles,
  onOpenLinkModal,
  onImageUpload,
  onDocxUpload,
  onVideoModalOpen,
  onTableModalOpen,
  onAiRefactor,
  content,
  isGenerating,
  isAiLoadingMinimized,
}) => {
  const imageFileInputRef = useRef(null);
  const docxFileInputRef = useRef(null);
  const [selectedValues, setSelectedValues] = useState({
    headings: "Paragraph",
    fontFamily: "Arial",
    fontSize: "Normal",
    lineHeight: "Single",
  });

  useEffect(() => {
    if (currentStyles && Object.keys(currentStyles).length > 0) {
      const newSelected = {};
      newSelected.headings =HEADINGS_MAP_REVERSE[currentStyles.formatBlock] || "Paragraph";
      newSelected.fontFamily =FONT_FACES.find((f) =>currentStyles.fontName?.toLowerCase().includes(f.toLowerCase())) || "Arial";
      newSelected.fontSize = FONT_SIZES_MAP_REVERSE[currentStyles.fontSize] || "Normal";
      let detectedLineHeightKey = "Single";
      const rawLineHeight = currentStyles.lineHeight;
      const rawFontSize = currentStyles.fontSize;

      if (rawLineHeight && rawLineHeight !== "normal") {
        if (rawLineHeight.endsWith("px") && rawFontSize && rawFontSize.endsWith("px")) {
          const lineHeightPx = parseFloat(rawLineHeight);
          const fontSizePx = parseFloat(rawFontSize);

          if (fontSizePx > 0) {
            const multiplier = (lineHeightPx / fontSizePx).toFixed(2);
            const matchingKey = Object.keys(LINE_HEIGHTS).find(
              (key) => parseFloat(LINE_HEIGHTS[key]).toFixed(2) === multiplier
            );
            detectedLineHeightKey = matchingKey || detectedLineHeightKey;
          }
        } else {
          detectedLineHeightKey =
            Object.keys(LINE_HEIGHTS).find(
              (key) => LINE_HEIGHTS[key] === rawLineHeight
            ) || detectedLineHeightKey;
        }
      } else if (rawLineHeight === "normal") {
        detectedLineHeightKey = "1.15";
      }
      newSelected.lineHeight = detectedLineHeightKey;
      setSelectedValues((prev) => ({ ...prev, ...newSelected }));
    }
  }, [currentStyles]);
  const handleDropdownChange = (dropdown, value) => {
    setSelectedValues((prev) => ({ ...prev, [dropdown]: value }));
  };

  return (
    <>
      <ModernToolBarStyle />
      <div className="modern-toolbar">
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Undo (Ctrl+Z)"
            onClick={() => applyStyle("undo")}
          >
            <Undo size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Redo (Ctrl+Y)"
            onClick={() => applyStyle("redo")}
          >
            <Redo size={18} />
          </ModernToolbarButton>
        </div>
        <div className="toolbar-separator" />
        <div className="toolbar-group">
          <ModernDropdown
            title="Headings"
            icon={<Pilcrow size={16} />}
            selectedValue={selectedValues.headings}
          >
            {Object.entries(HEADINGS).map(([key, value]) => (
              <button
                key={key}
                className="modern-dropdown-item"
                onClick={() => {
                  applyStyle("formatBlock", value);
                  handleDropdownChange("headings", key);
                }}
              >
                {key}
              </button>
            ))}
          </ModernDropdown>
          <ModernDropdown
            title="Font Family"
            icon={<Type size={16} />}
            selectedValue={selectedValues.fontFamily}
          >
            {FONT_FACES.map((face) => (
              <button
                key={face}
                className="modern-dropdown-item"
                style={{ fontFamily: face }}
                onClick={() => {
                  applyStyle("fontName", face);
                  handleDropdownChange("fontFamily", face);
                }}
              >
                {face}
              </button>
            ))}
          </ModernDropdown>
          <ModernDropdown
            title="Font Size"
            icon={<span style={{ fontSize: "17px" }}>F</span>}
            selectedValue={selectedValues.fontSize}
          >
            {Object.entries(FONT_SIZES).map(([key, value]) => (
              <button
                key={key}
                className="modern-dropdown-item"
                onClick={() => {
                  applyStyle("fontSize", value);
                  handleDropdownChange("fontSize", key);
                }}
              >
                {key}
              </button>
            ))}
          </ModernDropdown>
          <ModernDropdown
            title="Line Height"
            icon={<ArrowDownUp size={16} />}
            selectedValue={selectedValues.lineHeight}
          >
            {Object.entries(LINE_HEIGHTS).map(([key, value]) => (
              <button
                key={key}
                className="modern-dropdown-item"
                onClick={() => {
                  applyLineHeight(value);
                  handleDropdownChange("lineHeight", key);
                }}
              >
                {key}
              </button>
            ))}
          </ModernDropdown>
        </div>
        <div className="toolbar-separator" />
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Bold (Ctrl+B)"
            command="bold"
            activeStyles={activeStyles}
            onClick={() => applyStyle("bold")}
          >
            <Bold size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Italic (Ctrl+I)"
            command="italic"
            activeStyles={activeStyles}
            onClick={() => applyStyle("italic")}
          >
            <Italic size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Underline (Ctrl+U)"
            command="underline"
            activeStyles={activeStyles}
            onClick={() => applyStyle("underline")}
          >
            <Underline size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Strikethrough"
            command="strikeThrough"
            activeStyles={activeStyles}
            onClick={() => applyStyle("strikeThrough")}
          >
            <Strikethrough size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Superscript"
            command="superscript"
            activeStyles={activeStyles}
            onClick={() => applyStyle("superscript")}
          >
            <Superscript size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Subscript"
            command="subscript"
            activeStyles={activeStyles}
            onClick={() => applyStyle("subscript")}
          >
            <Subscript size={18} />
          </ModernToolbarButton>
          <ModernEmojiPicker applyStyle={applyStyle} />
        </div>

        <div className="toolbar-separator" />
        <div className="toolbar-group">
          <ModernColorPicker command="foreColor" applyStyle={applyStyle} />
          <ModernColorPicker command="backColor" applyStyle={applyStyle} />
        </div>

        <div className="toolbar-separator" />
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Align Left"
            command="justifyLeft"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyLeft")}
          >
            <AlignLeft size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Align Center"
            command="justifyCenter"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyCenter")}
          >
            <AlignCenter size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Align Right"
            command="justifyRight"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyRight")}
          >
            <AlignRight size={18} />
          </ModernToolbarButton>
        </div>
        <div className="toolbar-separator" />
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Ordered List"
            command="insertOrderedList"
            activeStyles={activeStyles}
            onClick={() => applyStyle("insertOrderedList")}
          >
            <ListOrdered size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Unordered List"
            command="insertUnorderedList"
            activeStyles={activeStyles}
            onClick={() => applyStyle("insertUnorderedList")}
          >
            <List size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Outdent"
            onClick={() => applyStyle("outdent")}
          >
            <Outdent size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Indent"
            onClick={() => applyStyle("indent")}
          >
            <Indent size={18} />
          </ModernToolbarButton>
        </div>
        <div className="toolbar-separator" />
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Blockquote"
            onClick={() => applyStyle("formatBlock", "<blockquote>")}
          >
            <Quote size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Code Block"
            onClick={() => applyStyle("formatBlock", "<pre>")}
          >
            <Code size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Add/Edit Link (Ctrl+K)"
            onClick={onOpenLinkModal}
          >
            <Link size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton
            title="Horizontal Line"
            onClick={() => applyStyle("insertHorizontalRule")}
          >
            <Minus size={18} />
          </ModernToolbarButton>
        </div>
        <div className="toolbar-separator" />
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Insert Image"
            onClick={() => imageFileInputRef.current?.click()}
          >
            <Image size={18} />
          </ModernToolbarButton>
          <ModernToolbarButton title="Insert Video" onClick={onVideoModalOpen}>
            <Video size={20} />
          </ModernToolbarButton>
          <ModernToolbarButton title="Insert Table" onClick={onTableModalOpen}>
            <Table size={18} />
          </ModernToolbarButton>
          <ModernDropdown title="Insert Columns" icon={<Columns size={18} />}>
            {Object.entries(COLUMN_LAYOUTS).map(([key, value]) => (
              <button
                key={key}
                className="modern-dropdown-item"
                onClick={() => applyStyle("insertHTML", value + "<p><br></p>")}
              >
                {key}
              </button>
            ))}
          </ModernDropdown>
          <ModernToolbarButton
            title="Import from Word (.docx)"
            onClick={() => docxFileInputRef.current?.click()}
          >
            <FileUp size={18} />
          </ModernToolbarButton>
        </div>
        <div className="toolbar-separator" />
        <div className="toolbar-group">
          <ModernToolbarButton
            title="Clear Formatting"
            onClick={() => applyStyle("removeFormat")}
          >
            <Eraser size={18} style={{ color: "red" }} />
          </ModernToolbarButton>
          <div className="toolbar-separator" />
          <ModernToolbarButton
            title="Improve with AI"
            onClick={onAiRefactor}
            disabled={!content || content === "" || isGenerating}
            className="modern-ai-button mx-0 rounded-3"
          >
            {isGenerating && !isAiLoadingMinimized ? (
              <div className="spinner"></div>
            ) : (
              <Sparkles size={20} />
            )}
          </ModernToolbarButton>
        </div>
      </div>
      <input
        type="file"
        accept="image/*"
        ref={imageFileInputRef}
        onChange={onImageUpload}
        style={{ display: "none" }}
      />
      <input
        type="file"
        accept=".docx"
        ref={docxFileInputRef}
        onChange={onDocxUpload}
        style={{ display: "none" }}
      />
    </>
  );
};
ModernToolbar.propTypes = { currentStyles: PropTypes.object, applyStyle: PropTypes.func.isRequired, applyLineHeight: PropTypes.func.isRequired, activeStyles: PropTypes.object.isRequired, onOpenLinkModal: PropTypes.func.isRequired, onImageUpload: PropTypes.func.isRequired, onDocxUpload: PropTypes.func.isRequired, onVideoModalOpen: PropTypes.func.isRequired, onTableModalOpen: PropTypes.func.isRequired, onAiRefactor: PropTypes.func.isRequired, content: PropTypes.string, isGenerating: PropTypes.bool.isRequired, isAiLoadingMinimized: PropTypes.bool.isRequired,};
export default ModernToolbar;