import { useState, useRef, useEffect, useCallback } from "react";
import ReactDOM from "react-dom";
import PropTypes from "prop-types";
import { ChevronDown } from "lucide-react";

const DropDownStyles=()=>(<style>{`.dropdown-menu{background-color:#fff;border:1px solid rgba(0,0,0,.15);border-radius:.375rem;box-shadow:0 .5rem 1rem rgba(0,0,0,.175);padding:.5rem}.dropdown-menu>*:not(div){display:block;width:100%;padding:.5rem 1rem;clear:both;font-weight:400;color:#212529;text-align:inherit;white-space:nowrap;background-color:transparent;border:0;text-decoration:none;border-radius:.25rem}.dropdown-menu>*:not(div):hover{background-color:#f8f9fa;color:#1e2125}.dropdown-menu div[class*="flex-wrap"] .btn{font-size:1.5rem;line-height:1;padding:.25rem;background-color:transparent;border:1px solid transparent;border-radius:.375rem;transition:background-color .15s ease-in-out}.dropdown-menu div[class*="flex-wrap"] .btn:hover{background-color:#007dfbff!important}.btn.forma-text-toolbar-button:hover{background-color:#e8f0fe!important;color:#1967d2!important}`}</style>);
const Dropdown = ({ title, icon, children, disabled = false, selectedValue,}) => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef(null);

  const handleToggle = useCallback(() => {
    setIsOpen((prev) => !prev);
  }, []);

  useEffect(() => {
    const handleClickOutside = (event) => {
      const menuNode = document.querySelector(".dropdown-menu-portal");
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target) &&
        (!menuNode || !menuNode.contains(event.target))
      ) {
        setIsOpen(false);
      }
    };
    if (isOpen) {
      document.addEventListener("mousedown", handleClickOutside);
    }
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [isOpen]);

  const MenuContent = () => {
    const menuRef = useRef(null);
    const [menuStyle, setMenuStyle] = useState({
      opacity: 0,
      position: "fixed",
    });

    useEffect(() => {
      if (dropdownRef.current && menuRef.current) {
        const buttonRect = dropdownRef.current.getBoundingClientRect();
        const menuWidth = menuRef.current.offsetWidth;
        const menuHeight = menuRef.current.offsetHeight;
        const viewportWidth = window.innerWidth;
        const viewportHeight = window.innerHeight;

        let top = buttonRect.bottom + 4;
        let left = buttonRect.left;

        if (left + menuWidth > viewportWidth) {
          left = buttonRect.right - menuWidth;
        }
        if (top + menuHeight > viewportHeight) {
          top = buttonRect.top - menuHeight - 4;
        }

        setMenuStyle({
          position: "fixed",
          top: `${Math.max(5, top)}px`,
          left: `${Math.max(5, left)}px`,
          zIndex: 2050,
          opacity: 1,
        });
      }
    }, []);

    return (
      <div
        ref={menuRef}
        className="dropdown-menu dropdown-menu-portal show"
        style={menuStyle}
        onClick={(e) => {if (e.target.closest("button")) { setIsOpen(false); } }}
      >
        {children}
      </div>
    );
  };

  return (
    <>
      <DropDownStyles />
      <div className="btn-group " ref={dropdownRef}>
        <button
          type="button"
          onClick={handleToggle}
          onMouseDown={(e) => e.preventDefault()}
          className="btn  d-flex align-items-center gap-1 py-2 px-2 forma-text-toolbar-button"
          aria-haspopup="true"
          aria-expanded={isOpen}
          title={title}
          disabled={disabled}
          style={{ minWidth: selectedValue ? "110px" : "auto" }}
        >
          {selectedValue ? (
            <span
              style={{
                overflow: "hidden",
                textOverflow: "ellipsis",
                whiteSpace: "nowrap",
                fontSize: "13px",
                textAlign: "left",
                flexGrow: 1,
              }}
            >
              <span className="me-2">{icon}</span> {selectedValue}
            </span>
          ) : (
            icon
          )}
          <ChevronDown size={14} />
        </button>
        {isOpen && ReactDOM.createPortal(<MenuContent />, document.body)}
      </div>
    </>
  );
};

Dropdown.propTypes = {
  title: PropTypes.string.isRequired,
  icon: PropTypes.node,
  children: PropTypes.node.isRequired,
  disabled: PropTypes.bool,
  selectedValue: PropTypes.string,
};

export default Dropdown;
