const FuturisticStatusbarStyles=()=>(<style>{`.futuristic-statusbar-v2{position:relative;width:98%;margin:10px auto 0;left:auto;transform:none;z-index:100;font-family:"Orbitron","Segoe UI",sans-serif;background:rgba(15,15,25,.8);margin-bottom:10px;color:var(--text-color,#e0e0e0);border:1px solid var(--border-color,rgba(138,43,226,.4));border-radius:12px;opacity:0}.futuristic-statusbar-v2.is-booted{opacity:1;transform:translateY(0)}.grid-background{position:absolute;top:0;left:0;right:0;bottom:0;background-image:linear-gradient(var(--glow-color,#00ffff) 1px,transparent 1px),linear-gradient(to right,var(--glow-color,#00ffff) 1px,transparent 1px);background-size:40px 40px;opacity:.05;animation:move-grid 20s linear infinite;z-index:1}@keyframes move-grid{from{background-position:0 0}to{background-position:40px 40px}}.statusbar-content{position:relative;display:flex;justify-content:space-between;align-items:center;padding:10px 25px;z-index:2}.corner-bracket{position:absolute;width:20px;height:20px;border-style:solid;border-color:var(--glow-color,#00ffff);opacity:0;transition:opacity 1s ease .5s}.futuristic-statusbar-v2.is-booted .corner-bracket{opacity:.5}.corner-bracket.top-left{top:-2px;left:-2px;border-width:2px 0 0 2px;border-radius:8px 0 0 0}.corner-bracket.top-right{top:-2px;right:-2px;border-width:2px 2px 0 0;border-radius:0 8px 0 0}.corner-bracket.bottom-left{bottom:-2px;left:-2px;border-width:0 0 2px 2px;border-radius:0 0 0 8px}.corner-bracket.bottom-right{bottom:-2px;right:-2px;border-width:0 2px 2px 0;border-radius:0 0 8px 0}.status-group-v2{display:flex;align-items:center;gap:24px}.data-readout{display:flex;align-items:center;gap:8px;color:#aab5c3}.data-icon{color:var(--glow-color,#00ffff);filter:drop-shadow(0 0 3px var(--glow-color,#00ffff))}.data-label{font-size:12px;font-weight:600;letter-spacing:1px;opacity:.6}.data-value{font-family:"Roboto Mono",monospace;font-size:16px;font-weight:700;color:#fff}.action-button{background:transparent;border:1px solid transparent;color:#aab5c3;width:36px;height:36px;display:inline-flex;align-items:center;justify-content:center;border-radius:50%;cursor:pointer;position:relative;transition:all .3s cubic-bezier(.25,.8,.25,1)}.action-button::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;border-radius:50%;border:1px solid var(--border-color,rgba(138,43,226,.4));transition:all .3s ease}.action-button:hover{color:#fff}.action-button:hover::before{transform:scale(1.2);opacity:0}.action-button:active{transform:scale(.9)}.futuristic-dropdown{position:relative}.futuristic-dropdown-menu{position:absolute;top:calc(100% + 8px);left:0;background:var(--bg-color);backdrop-filter:blur(12px) saturate(180%);border:1px solid var(--border-color);border-radius:8px;padding:8px;z-index:1001;width:max-content;min-width:150px;opacity:0;visibility:hidden;transform:translateY(10px) scale(.95);transform-origin:top left;transition:all var(--animation-speed) cubic-bezier(.16,1,.3,1)}.futuristic-dropdown-menu.show{opacity:1;visibility:visible;transform:translateY(0) scale(1)}.futuristic-dropdown-item{background:none;border:none;color:var(--text-color);padding:8px 12px;border-radius:6px;cursor:pointer;display:block;width:100%;text-align:left;transition:background var(--animation-speed) ease,color var(--animation-speed) ease}.futuristic-dropdown-item:hover{background:var(--hover-bg);color:#fff}.chevron-icon{transition:transform var(--animation-speed) ease}.chevron-icon.open{transform:rotate(180deg)}.futuristic-color-picker{display:grid;grid-template-columns:repeat(5,1fr);gap:10px 4px;width:160px}.color-swatch-hexagon{width:30px;height:34.64px;background:none;border:none;cursor:pointer;position:relative;transition:transform var(--animation-speed) ease}.color-swatch-hexagon:hover{transform:scale(1.15) rotate(10deg)}.hexagon-inner{position:absolute;top:0;left:0;width:100%;height:100%;clip-path:polygon(50% 0%,100% 25%,100% 75%,50% 100%,0% 75%,0% 25%);border:1px solid rgba(255,255,255,.2)}.futuristic-emoji-picker{display:grid;grid-template-columns:repeat(4,1fr);gap:8px}.futuristic-emoji{font-size:24px;background:none;border:none;padding:4px;border-radius:8px;cursor:pointer;transition:background var(--animation-speed) ease,transform var(--animation-speed) ease}.futuristic-emoji:hover{background:var(--hover-bg);transform:scale(1.2)}`}</style>);
export default FuturisticStatusbarStyles;