import PropTypes from "prop-types";
import { Smile } from "lucide-react";
import ModernDropdown from "./ModernDropdown";
const EMOJIS = [
  "💻",
  "🖥️",
  "🖱️",
  "⌨️",
  "🖨️",
  "📱",
  "📲",
  "🕹️",
  "🧑‍💻",
  "👨‍💻",
  "👩‍💻",
  "🔧",
  "🔨",
  "⚙️",
  "🧰",
  "📡",
  "🌐",
  "🔌",
  "💾",
  "📂",
  "📁",
  "🗂️",
  "📝",
  "📊",
  "📈",
  "📉",
  "🔒",
  "🔓",
  "🛡️",
  "🐞",
  "⚡",
  "🚀",
  "🧪",
  "💡",
  "🤖",
  "🧩",
  "🧬",
  "🛠️",
  "📧",
  "🔍",
  "📥",
  "📤",
];
const ModernEmojiPicker = ({ applyStyle }) => (
  <ModernDropdown title="Insert Emoji" icon={<Smile size={18} />}>
    <div className="modern-emoji-picker">
      {EMOJIS.map((emoji) => (
        <button key={emoji} type="button" className="modern-emoji" onClick={() => applyStyle("insertText", emoji)}>{emoji}</button>
      ))}
    </div>
  </ModernDropdown>
);
ModernEmojiPicker.propTypes = { applyStyle: PropTypes.func.isRequired,};
export default ModernEmojiPicker;
