import { useState } from "react";
import PropTypes from "prop-types";
import { Table } from "lucide-react";
import BaseModal from "./BaseModal";

const TableModal = ({ isOpen, onClose, onCreate }) => {
  const [tableData, setTableData] = useState({ rows: 3, cols: 3 });
  if (!isOpen) return null;
  return (
    <BaseModal onClose={onClose} title="Create Table" icon={<Table size={20} className="text-primary" />}>
      <div className="modal-body p-4">
        <div className="mb-3">
          <label className="form-label fw-semibold">Rows</label>
          <input type="number" className="form-control" value={tableData.rows} min="1" max="20"
            onChange={(e) =>
              setTableData({
                ...tableData,
                rows: parseInt(e.target.value, 10) || 1,
              })
            }
          />
        </div>
        <div>
          <label className="form-label fw-semibold">Columns</label>
          <input type="number" className="form-control" value={tableData.cols} min="1" max="30"
            onChange={(e) =>
              setTableData({
                ...tableData,
                cols: parseInt(e.target.value, 10) || 1,
              })
            }
          />
        </div>
      </div>
      <div className="modal-footer border-0 bg-light">
        <button type="button" className="btn editor-secondary"  onClick={onClose}>Cancel </button>
        <button type="button" className="btn editor-primary" onClick={() => onCreate(tableData)}>Create</button>
      </div>
    </BaseModal>
  );
};
TableModal.propTypes = {isOpen: PropTypes.bool.isRequired, onClose: PropTypes.func.isRequired,onCreate: PropTypes.func.isRequired,};
export default TableModal;
