import { useEffect, useRef, useState } from "react";
import PropTypes from "prop-types";
import {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  Code,
  Link,
  Image,
  List,
  ListOrdered,
  Quote,
  AlignLeft,
  AlignCenter,
  AlignRight,
  Indent,
  Outdent,
  Table,
  Pilcrow,
  Eraser,
  Undo,
  Redo,
  Columns,
  Minus,
  Type,
  ArrowDownUp,
  FileUp,
  Superscript,
  Subscript,
  Video,
  Sparkles,
} from "lucide-react";
import ToolbarButton from "./components/ToolbarButton";
import Dropdown from "./components/Dropdown";
import ColorPicker from "./components/ColorPicker";
import EmojiPicker from "./components/EmojiPicker";

const FONT_FACES = [
  "Arial",
  "Georgia",
  "Helvetica",
  "Tahoma",
  "Times New Roman",
  "Verdana",
  "Courier New",
];
const FONT_SIZES = { Small: "2", Normal: "3", Large: "5", Huge: "7" };
const HEADINGS = {
  "Heading 1": "<h1>",
  "Heading 2": "<h2>",
  "Heading 3": "<h3>",
  "Heading 4": "<h4>",
  "Heading 5": "<h5>",
  Paragraph: "<p>",
};
const LINE_HEIGHTS = { Single: "1", 1.15: "1.15", 1.5: "1.5", Double: "2" };
const FONT_SIZES_MAP_REVERSE = {
  "13.3333px": "Small",
  "16px": "Normal",
  "24px": "Large",
  "42.6667px": "Huge",
};
const HEADINGS_MAP_REVERSE = {
  h1: "Heading 1",
  h2: "Heading 2",
  h3: "Heading 3",
  h4: "Heading 4",
  h5: "Heading 5",
  p: "Paragraph",
};

const columnTableStyle ="width: 100%; border-collapse: collapse; table-layout: fixed;  margin: 0;  padding: 0;";
const columnCellStyle = "padding: 0; vertical-align: top;";
const responsiveCss = `<style>@media screen and (max-width: 600px) { .responsive-cell { display: block !important; width: 100% !important; box-sizing: border-box !important; } }</style>`;
const COLUMN_LAYOUTS = {
  "2 Columns (1:1)": `${responsiveCss}<table class="responsive-table" style="${columnTableStyle}"><tbody><tr><td class="responsive-cell" style="${columnCellStyle} width: 50%;"><p>C1</p></td><td class="responsive-cell" style="${columnCellStyle} width: 50%;"><p>C2</p></td></tr></tbody></table>`,
  "3 Columns (1:1:1)": `${responsiveCss}<table class="responsive-table" style="${columnTableStyle}"><tbody><tr><td class="responsive-cell" style="${columnCellStyle} width: 33.33%;"><p>C1</p></td><td class="responsive-cell" style="${columnCellStyle} width: 33.33%;"><p>C2</p></td><td class="responsive-cell" style="${columnCellStyle} width: 33.33%;"><p>C3</p></td></tr></tbody></table>`,
  "2 Columns (1:2)": `${responsiveCss}<table class="responsive-table" style="${columnTableStyle}"><tbody><tr><td class="responsive-cell" style="${columnCellStyle} width: 33.33%;"><p>C1</p></td><td class="responsive-cell" style="${columnCellStyle} width: 66.66%;"><p>C2</p></td></tr></tbody></table>`,
  "2 Columns (2:1)": `${responsiveCss}<table class="responsive-table" style="${columnTableStyle}"><tbody><tr><td class="responsive-cell" style="${columnCellStyle} width: 66.66%;"><p>C1</p></td><td class="responsive-cell" style="${columnCellStyle} width: 33.33%;"><p>C2</p></td></tr></tbody></table>`,
};

const ClassicToolbar = ({
  currentStyles,
  applyStyle,
  applyLineHeight,
  activeStyles,
  onOpenLinkModal,
  onImageUpload,
  onDocxUpload,
  onVideoModalOpen,
  onTableModalOpen,
  onAiRefactor,
  content,
  isGenerating,
  isAiLoadingMinimized,
}) => {
  const toolbarRef = useRef(null);
  const imageFileInputRef = useRef(null);
  const docxFileInputRef = useRef(null);

  const [selectedValues, setSelectedValues] = useState({
    headings: "Paragraph",
    fontFamily: "Arial",
    fontSize: "Normal",
    lineHeight: "Single",
  });

  useEffect(() => {
    if (currentStyles && Object.keys(currentStyles).length > 0) {
      const newSelected = {};

      newSelected.headings = HEADINGS_MAP_REVERSE[currentStyles.formatBlock] || "Paragraph";
      newSelected.fontFamily = FONT_FACES.find((f) =>
          currentStyles.fontName?.toLowerCase().includes(f.toLowerCase())
        ) || "Arial";
      newSelected.fontSize =
        Object.entries(FONT_SIZES_MAP_REVERSE).find(([px]) =>
          currentStyles.fontSize?.includes(px)
        )?.[1] || "Normal";

      let detectedLineHeightKey = "Single";
      const rawLineHeight = currentStyles.lineHeight;
      const rawFontSize = currentStyles.fontSize;

      if (rawLineHeight && rawLineHeight !== "normal") {
        if (rawLineHeight.endsWith("px") && rawFontSize?.endsWith("px")) {
          const lineHeightPx = parseFloat(rawLineHeight);
          const fontSizePx = parseFloat(rawFontSize);
          if (fontSizePx > 0) {
            const multiplier = (lineHeightPx / fontSizePx).toFixed(2);
            detectedLineHeightKey =
              Object.keys(LINE_HEIGHTS).find(
                (key) => parseFloat(LINE_HEIGHTS[key]).toFixed(2) === multiplier
              ) || detectedLineHeightKey;
          }
        } else {
          detectedLineHeightKey =
            Object.keys(LINE_HEIGHTS).find(
              (key) => LINE_HEIGHTS[key] === rawLineHeight
            ) || detectedLineHeightKey;
        }
      } else if (rawLineHeight === "normal") {
        detectedLineHeightKey = "1.15";
      }
      newSelected.lineHeight = detectedLineHeightKey;
      setSelectedValues((prev) => ({ ...prev, ...newSelected }));
    }
  }, [currentStyles]);

  const handleDropdownChange = (dropdown, value) => {
    setSelectedValues((prev) => ({ ...prev, [dropdown]: value }));
  };

  useEffect(() => {
    if (toolbarRef.current && typeof window.bootstrap !== "undefined") {
      const tooltipTriggerList = [].slice.call(
        toolbarRef.current.querySelectorAll('[data-bs-toggle="tooltip"]')
      );
      const tooltipList = tooltipTriggerList.map(
        (el) => new window.bootstrap.Tooltip(el)
      );
      return () => tooltipList.forEach((tooltip) => tooltip?.dispose());
    }
  }, []);

  return (
    <>
      <div ref={toolbarRef} className="p-2 border-bottom d-flex flex-wrap align-items-center gap-2" >
        <div className="btn-group" role="group">
          <ToolbarButton title="Undo (Ctrl+Z)" onClick={() => applyStyle("undo")}><Undo size={16} /></ToolbarButton>
          <ToolbarButton title="Redo (Ctrl+Y)" onClick={() => applyStyle("redo")} ><Redo size={16} /></ToolbarButton>
        </div>
        <div className="btn-group">
          <Dropdown title="Headings" selectedValue={selectedValues.headings}>
            {Object.entries(HEADINGS).map(([key, value]) => (
              <button key={key} className="dropdown-item" onClick={() => {
                  applyStyle("formatBlock", value);
                  handleDropdownChange("headings", key);
                }}
              >{key} </button>
            ))}
          </Dropdown>
          <Dropdown title="Font Family" selectedValue={selectedValues.fontFamily}>
            {FONT_FACES.map((face) => (
              <button key={face} className="dropdown-item" style={{ fontFamily: face }} onClick={() => {
                  applyStyle("fontName", face);
                  handleDropdownChange("fontFamily", face);
                }}
              > {face}</button>
            ))}
          </Dropdown>
          <Dropdown title="Font Size" selectedValue={selectedValues.fontSize}>
            {Object.entries(FONT_SIZES).map(([key, value]) => (
              <button key={key} className="dropdown-item" onClick={() => {
                  applyStyle("fontSize", value);
                  handleDropdownChange("fontSize", key);
                }}
              >{key}</button>
            ))}
          </Dropdown>
          <Dropdown title="Line Height" selectedValue={selectedValues.lineHeight}
          >
            {Object.entries(LINE_HEIGHTS).map(([key, value]) => (
              <button key={key} className="dropdown-item" style={{ fontSize: "14px" }} onClick={() => {
                  applyLineHeight(value);
                  handleDropdownChange("lineHeight", key);
                }}
              >{key} </button>
            ))}
            <button className="dropdown-item text-danger border-top mt-2" style={{ fontSize: "14px" }} onClick={() => {
                applyLineHeight("normal");
                handleDropdownChange("lineHeight", "Single");
              }}
            > Remove Height </button>
          </Dropdown>
        </div>

        <div className="btn-group">
          <ToolbarButton title="Bold (Ctrl+B)" command="bold" activeStyles={activeStyles} onClick={() => applyStyle("bold")}>
            <Bold size={14} />
          </ToolbarButton>
          <ToolbarButton title="Italic (Ctrl+I)" command="italic" activeStyles={activeStyles} onClick={() => applyStyle("italic")}>
            <Italic size={16} />
          </ToolbarButton>
          <ToolbarButton title="Underline (Ctrl+U)" command="underline" activeStyles={activeStyles} onClick={() => applyStyle("underline")}>
            <Underline size={16} />
          </ToolbarButton>
          <ToolbarButton  title="Strikethrough"  command="strikeThrough"  activeStyles={activeStyles}  onClick={() => applyStyle("strikeThrough")}>
            <Strikethrough size={16} />
          </ToolbarButton>
          <ToolbarButton title="Superscript" command="superscript" activeStyles={activeStyles} onClick={() => applyStyle("superscript")}>
            <Superscript size={16} />
          </ToolbarButton>
          <ToolbarButton title="Subscript" command="subscript" activeStyles={activeStyles} onClick={() => applyStyle("subscript")}>
            <Subscript size={16} />
          </ToolbarButton>
          <EmojiPicker applyStyle={applyStyle} />
        </div>
        <div className="btn-group">
          <ColorPicker command="foreColor" applyStyle={applyStyle} currentStyles={currentStyles}/>
          <ColorPicker command="backColor" applyStyle={applyStyle} currentStyles={currentStyles} />
        </div>
        <div className="btn-group">
          <ToolbarButton title="Align Left" command="justifyLeft" activeStyles={activeStyles} onClick={() => applyStyle("justifyLeft")} >
            <AlignLeft size={16} />
          </ToolbarButton>
          <ToolbarButton title="Align Center" command="justifyCenter" activeStyles={activeStyles} onClick={() => applyStyle("justifyCenter")}>
            <AlignCenter size={16} />
          </ToolbarButton>
          <ToolbarButton title="Align Right" command="justifyRight" activeStyles={activeStyles} onClick={() => applyStyle("justifyRight")}>
            <AlignRight size={16} />
          </ToolbarButton>
        </div>
        <div className="btn-group">
          <ToolbarButton title="Ordered List" command="insertOrderedList" activeStyles={activeStyles} onClick={() => applyStyle("insertOrderedList")} >
            <ListOrdered size={16} />
          </ToolbarButton>
          <ToolbarButton title="Unordered List" command="insertUnorderedList" activeStyles={activeStyles} onClick={() => applyStyle("insertUnorderedList")}>
            <List size={16} />
          </ToolbarButton>
          <ToolbarButton title="Outdent" onClick={() => applyStyle("outdent")}>
            <Outdent size={16} />
          </ToolbarButton>
          <ToolbarButton title="Indent" onClick={() => applyStyle("indent")}>
            <Indent size={16} />
          </ToolbarButton>
        </div>
        <div className="btn-group">
          <ToolbarButton title="Blockquote" onClick={() => applyStyle("formatBlock", "<blockquote>")}>
            <Quote size={16} />
          </ToolbarButton>
          <ToolbarButton title="Code Block" onClick={() => applyStyle("formatBlock", "<pre>")}>
            <Code size={16} />
          </ToolbarButton>
          <ToolbarButton title="Add/Edit Link (Ctrl+K)" onClick={onOpenLinkModal}>
            <Link size={16} />
          </ToolbarButton>
          <ToolbarButton title="Horizontal Line" onClick={() => applyStyle("insertHorizontalRule")}>
            <Minus size={16} />
          </ToolbarButton>
          <ToolbarButton title="Clear Formatting" onClick={() => applyStyle("removeFormat")}>
            <Eraser size={16} />
          </ToolbarButton>
        </div>
        <div className="btn-group">
          <ToolbarButton title="Insert Image" onClick={() => imageFileInputRef.current?.click()}>
            <Image size={16} />
          </ToolbarButton>
          <ToolbarButton title="Insert Video" onClick={onVideoModalOpen}>
            <Video size={16} />
          </ToolbarButton>
          <ToolbarButton title="Insert Table" onClick={onTableModalOpen}>
            <Table size={16} />
          </ToolbarButton>
          <Dropdown title="Insert Columns" icon={<Columns size={16} />}>
            {Object.entries(COLUMN_LAYOUTS).map(([key, value]) => (
              <button key={key} className="dropdown-item" style={{ fontSize: "14px" }} onClick={() => applyStyle("insertHTML", value + "<p><br></p>")}>{key}</button>
            ))}
          </Dropdown>
          <ToolbarButton title="Import from Word (.docx)" onClick={() => docxFileInputRef.current?.click()}>
            <FileUp size={16} />
          </ToolbarButton>
        </div>
        <div className="btn-group">
          <ToolbarButton title="Improve with AI" onClick={onAiRefactor} disabled={!content || content === "" || isGenerating}>
            {isGenerating && !isAiLoadingMinimized ? (
              <div className="spinner-border spinner-border-sm" role="status"><span className="visually-hidden">Loading...</span></div>
            ) : (
              <Sparkles size={16} />
            )}
          </ToolbarButton>
        </div>
      </div>
      <input type="file" accept="image/*" ref={imageFileInputRef} onChange={onImageUpload} className="d-none"/>
      <input type="file" accept=".docx" ref={docxFileInputRef} onChange={onDocxUpload} className="d-none"/>
    </>
  );
};
ClassicToolbar.propTypes = { currentStyles: PropTypes.object, applyStyle: PropTypes.func.isRequired, applyLineHeight: PropTypes.func.isRequired, activeStyles: PropTypes.object.isRequired, onOpenLinkModal: PropTypes.func.isRequired, onImageUpload: PropTypes.func.isRequired, onDocxUpload: PropTypes.func.isRequired, onVideoModalOpen: PropTypes.func.isRequired, onTableModalOpen: PropTypes.func.isRequired, onAiRefactor: PropTypes.func.isRequired, content: PropTypes.string, isGenerating: PropTypes.bool.isRequired, isAiLoadingMinimized: PropTypes.bool.isRequired,};
export default ClassicToolbar;