import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Palette, Highlighter } from "lucide-react";
import Dropdown from "./Dropdown";
import ColorPickerStyles from "../../../../../styles/theme/classic/ColorPicker.styles";

const COLORS = [
  "#000000",
  "#222222",
  "#444444",
  "#666666",
  "#888888",
  "#999999",
  "#BBBBBB",
  "#CCCCCC",
  "#DDDDDD",
  "#EEEEEE",
  "#FFFFFF",
  "#FF0000",
  "#CC0000",
  "#990000",
  "#660000",
  "#FF6666",
  "#FF9900",
  "#FF6600",
  "#CC6600",
  "#FFB84D",
  "#FFFF00",
  "#FFEE00",
  "#CCCC00",
  "#999900",
  "#00FF00",
  "#00CC00",
  "#009900",
  "#66FF66",
  "#33CC33",
  "#00FFFF",
  "#00CCCC",
  "#009999",
  "#66FFFF",
  "#0000FF",
  "#0000CC",
  "#000099",
  "#6666FF",
  "#3366FF",
  "#9900FF",
  "#6600CC",
  "#9933FF",
  "#CC99FF",
  "#FF00FF",
  "#CC00CC",
  "#FF66FF",
  "#FF99FF",
];

const ColorPicker = ({ command, applyStyle, currentStyles }) => {
  const [selectedColor, setSelectedColor] = useState(command === "foreColor" ? "#000000" : "#ffff00");
  const title = command === "foreColor" ? "Text Color" : "Highlight Color";

  useEffect(() => {
    if (currentStyles) {
      const colorValue =
        command === "foreColor"
          ? currentStyles.foreColor
          : currentStyles.backColor;
      if (colorValue) {
        setSelectedColor(colorValue);
      }
    }
  }, [currentStyles, command]);

  const handleColorSelect = (color) => {
    setSelectedColor(color);
    applyStyle(command, color);
  };

  const icon = (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        gap: "4px",
      }}
    >
      {command === "foreColor" ? (<Palette size={18} />
      ) : (<Highlighter size={18} />)}
      <div style={{ width: "15px", height: "4px", backgroundColor: selectedColor, borderRadius: "2px", border: "1px solid #ced4da",}}/>
    </div>
  );

  return (
    <>
      <ColorPickerStyles />
      <Dropdown title={title} icon={icon}>
        <div className="p-3 d-flex flex-wrap gap-1" style={{ width: "160px" }}>
          {COLORS.map((color) => (
            <button key={color} type="button" className="color-swatch" style={{ backgroundColor: color }} onClick={() => handleColorSelect(color)} aria-label={color}/>
          ))}
        </div>
      </Dropdown>
    </>
  );
};

ColorPicker.propTypes = { command: PropTypes.oneOf(["foreColor", "backColor"]).isRequired, applyStyle: PropTypes.func.isRequired, currentStyles: PropTypes.object,};
export default ColorPicker;