import PropTypes from "prop-types";
import { Info } from "lucide-react";
import BaseModal from "./BaseModal";

const InfoModal = ({ isOpen, onClose, title, message }) => {
  if (!isOpen) return null;

  return (
    <BaseModal onClose={onClose} title={title} icon={<Info size={20} className="text-primary" />} >
      <div className="modal-body p-4"><p>{message}</p></div>
      <div className="modal-footer border-0 bg-light"> <button type="button" className="btn editor-primary" onClick={onClose}>OK</button></div>
    </BaseModal>
  );
};
InfoModal.propTypes = { isOpen: PropTypes.bool, onClose: PropTypes.func,title: PropTypes.string, message: PropTypes.string,};
export default InfoModal;