import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Palette, Highlighter } from "lucide-react";
import FuturisticDropdown from "./FuturisticDropdown";

const COLORS = [
  "#000000",
  "#222222",
  "#444444",
  "#666666",
  "#888888",
  "#999999",
  "#BBBBBB",
  "#CCCCCC",
  "#DDDDDD",
  "#EEEEEE",
  "#FFFFFF",
  "#FF0000",
  "#CC0000",
  "#990000",
  "#660000",
  "#FF6666",
  "#FF9900",
  "#FF6600",
  "#CC6600",
  "#FFB84D",
  "#FFFF00",
  "#FFEE00",
  "#CCCC00",
  "#999900",
  "#00FF00",
  "#00CC00",
  "#009900",
  "#66FF66",
  "#33CC33",
  "#00FFFF",
  "#00CCCC",
  "#009999",
  "#66FFFF",
  "#0000FF",
  "#0000CC",
  "#000099",
  "#6666FF",
  "#3366FF",
  "#9900FF",
  "#6600CC",
  "#9933FF",
  "#CC99FF",
  "#FF00FF",
  "#CC00CC",
  "#FF66FF",
  "#FF99FF",
];

const FuturisticColorPicker = ({ command, applyStyle, currentStyles }) => {
  const [selectedColor, setSelectedColor] = useState(
    command === "foreColor" ? "#FFFFFF" : "#00FFFF"
  );
  const title = command === "foreColor" ? "Text Color" : "Highlight Color";

  useEffect(() => {
    if (currentStyles) {
      const colorValue = command === "foreColor"? currentStyles.foreColor: currentStyles.backColor;
      if (colorValue) {setSelectedColor(colorValue);}
    }
  }, [currentStyles, command]);

  const handleColorSelect = (color) => {
    setSelectedColor(color);
    applyStyle(command, color);
  };

  const icon = (
    <div
      style={{
        position: "relative",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      {command === "foreColor" ? (
        <Palette size={18} />
      ) : (
        <Highlighter size={18} />
      )}
      <div
        style={{
          position: "absolute",
          bottom: "-4px",
          width: "6px",
          height: "6px",
          backgroundColor: selectedColor,
          borderRadius: "50%",
          boxShadow: `0 0 5px ${selectedColor}`,
        }}
      />
    </div>
  );

  return (
    <FuturisticDropdown title={title} icon={icon}>
      <div className="futuristic-color-picker">
        {COLORS.map((color) => (
          <button
            key={color}
            type="button"
            className="color-swatch-hexagon"
            onClick={() => handleColorSelect(color)}
            aria-label={color}
          >
            <div className="hexagon-inner" style={{ backgroundColor: color }} />
          </button>
        ))}
      </div>
    </FuturisticDropdown>
  );
};

FuturisticColorPicker.propTypes = {
  command: PropTypes.oneOf(["foreColor", "backColor"]).isRequired,
  applyStyle: PropTypes.func.isRequired,
  currentStyles: PropTypes.object,
};

export default FuturisticColorPicker;
