import PropTypes from "prop-types";

const Editor = ({
  editorRef,
  content,
  showSource,
  onContentChange,
  onKeyDown,
  textContentClass,
}) => {
  if (showSource) {
    return (
    <textarea value={content || ""} onChange={onContentChange} className="form-control rounded-0 border-0  font-monospace small bg-dark text-light forma-text-source-view"/>
  );
  }
  return (
    <div
     ref={editorRef}
      contentEditable
      suppressContentEditableWarning
      className={`forma-text-editor-content form-control rounded-0 border-0 h-100 p-3 ${textContentClass}`}
      style={{ minHeight: "60vh" }}
      onInput={onContentChange}
      onKeyDown={onKeyDown}
    />
  );
};
Editor.propTypes = { editorRef: PropTypes.object.isRequired, content: PropTypes.string, showSource: PropTypes.bool.isRequired, onContentChange: PropTypes.func.isRequired, onKeyDown: PropTypes.func.isRequired, textContentClass: PropTypes.string,};
export default Editor;