import PropTypes from "prop-types";
import { CodeXml, Maximize, Minimize } from "lucide-react";
import ModernStatusBarStyles from "../../../../styles/theme/modern/ModernStatusBar.styles";

const ModernStatusBar = ({ wordCount, charCount, onToggleSource, isFullScreen, onToggleFullScreen,}) => (
  <>
    <ModernStatusBarStyles />
    <div className="modern-statusbar">
      <div className="statusbar-segment">
        <span className="statusbar-item">Words: {wordCount}</span>
        <span className="statusbar-divider">|</span>
        <span className="statusbar-item">Characters: {charCount}</span>
      </div>
      <div className="statusbar-segment">
        <button className="statusbar-button" title="View HTML Source" onClick={onToggleSource}><CodeXml size={18} /></button>
        <button className="statusbar-button" title={isFullScreen ? "Exit Full Screen" : "Full Screen"} onClick={onToggleFullScreen}>
          {isFullScreen ? <Minimize size={18} /> : <Maximize size={18} />}
        </button>
      </div>
    </div>
  </>
);
ModernStatusBar.propTypes = { wordCount: PropTypes.number.isRequired, charCount: PropTypes.number.isRequired, onToggleSource: PropTypes.func.isRequired, onToggleFullScreen: PropTypes.func.isRequired, isFullScreen: PropTypes.bool.isRequired,};
export default ModernStatusBar;