import PropTypes from "prop-types";
import { CodeXml, Maximize, Minimize } from "lucide-react";
import StatusbarButton from "../StatusbarButton";

const ClassicStatusbar = ({wordCount,charCount,onToggleSource,showSource,onToggleFullScreen,isFullScreen,}) => (
  <div className="flex-shrink-0 p-2 border-top bg-light d-flex justify-content-between align-items-center small text-muted">
    <div>
      <span className="footer-text-area">Words: {wordCount}</span>
      <span className="mx-2">|</span>
      <span className="footer-text-area">Characters: {charCount}</span>
    </div>
    <div className="btn-group">
      <StatusbarButton title="View HTML Source" onClick={onToggleSource}><CodeXml size={16} /></StatusbarButton>
      <StatusbarButton title={isFullScreen ? "Exit Full Screen" : "Full Screen"} onClick={onToggleFullScreen}>
        {isFullScreen ? <Minimize size={16} /> : <Maximize size={16} />}
      </StatusbarButton>
    </div>
  </div>
);
ClassicStatusbar.propTypes = { wordCount: PropTypes.number.isRequired, charCount: PropTypes.number.isRequired, onToggleSource: PropTypes.func.isRequired, showSource: PropTypes.bool.isRequired, onToggleFullScreen: PropTypes.func.isRequired, isFullScreen: PropTypes.bool.isRequired, isPremium: PropTypes.bool,};
export default ClassicStatusbar;