import React from "react";
import PropTypes from "prop-types";

const ToolbarButton = React.memo(
  ({ onClick, title, command, activeStyles, disabled = false, children }) => (
    <>
      <button type="button" onClick={onClick} onMouseDown={(e) => e.preventDefault()} className={`btn btn-outline-secondary ${activeStyles && activeStyles.has(command) ? "active" : ""}`}
        data-bs-toggle="tooltip"
        data-bs-placement="bottom"
        disabled={disabled}
      >
        {children}
      </button>
    </>
  )
);

ToolbarButton.propTypes = { onClick: PropTypes.func.isRequired, title: PropTypes.string.isRequired, command: PropTypes.string, activeStyles: PropTypes.object, disabled: PropTypes.bool, children: PropTypes.node.isRequired,};
ToolbarButton.displayName = "ToolbarButton";
export default ToolbarButton;