const FuturisticToolbarStyles=()=>(<style>{`@keyframes pulse-glow{0%,100%{filter:drop-shadow(0 0 3px var(--glow-color-faded))}50%{filter:drop-shadow(0 0 8px var(--glow-color))}}@keyframes scanline{0%{transform:translateY(-100%)}100%{transform:translateY(100%)}}@keyframes subtle-breathing{0%,100%{box-shadow:0 0 20px rgba(0,255,255,0.1),inset 0 0 5px rgba(138,43,226,0.2)}50%{box-shadow:0 0 25px rgba(0,255,255,0.2),inset 0 0 8px rgba(138,43,226,0.3)}}:root{--bg-color:rgba(15,18,32,.7);--border-color:rgba(0,255,255,.25);--glow-color:#0ff;--glow-color-faded:rgba(0,255,255,.5);--text-color:#e0e0e0;--active-bg:rgba(0,255,255,.1);--hover-bg:rgba(255,255,255,.08);--animation-speed:.1s}.futuristic-toolbar{position:fixed;display:flex;align-items:stretch;padding:8px;background:var(--bg-color);backdrop-filter:blur(12px) saturate(150%);border-radius:12px;border:1px solid var(--border-color);z-index:500;color:var(--text-color);animation:subtle-breathing 6s infinite ease-in-out;transition:all var(--animation-speed) ease;transform:none}.futuristic-toolbar.docked{position:relative;width:auto;max-width:98%;top:auto!important;left:auto!important;transform:none!important;margin:8px auto;animation:none;backdrop-filter:none;background:rgba(15,15,25,.8)}.futuristic-toolbar::before,.futuristic-toolbar::after{content:'';position:absolute;top:0;left:0;width:100%;height:100%;z-index:-1;pointer-events:none;border-radius:12px}.futuristic-toolbar::before{background-image:linear-gradient(var(--border-color) 1px,transparent 1px),linear-gradient(to right,var(--border-color) 1px,transparent 1px);background-size:30px 30px;opacity:.1}.futuristic-toolbar::after{background:linear-gradient(to bottom,transparent 0%,var(--glow-color-faded) 50%,transparent 100%);opacity:.05;animation:scanline 8s linear infinite}.futuristic-toolbar.docked::before,.futuristic-toolbar.docked::after{display:none}.toolbar-content{display:flex;align-items:start;flex-wrap:wrap;gap:4px;justify-content:start}.drag-handle{display:flex;align-items:center;justify-content:center;padding:0 8px;margin-right:8px;border-right:1px solid var(--border-color);cursor:grab;color:var(--glow-color);transition:opacity var(--animation-speed) ease,color var(--animation-speed) ease;animation:pulse-glow 4s infinite ease-in-out}.drag-handle:hover{animation-play-state:paused;filter:drop-shadow(0 0 10px var(--glow-color))}.drag-handle:active{cursor:grabbing;color:#fff}.toolbar-group{display:flex;gap:4px}.toolbar-separator{width:2px;height:20px;align-self:center;background:var(--border-color);margin:0 8px;opacity:.5;box-shadow:0 0 5px var(--glow-color-faded)}.futuristic-button{background:transparent;border:1px solid transparent;color:var(--text-color);width:38px;height:38px;display:inline-flex;align-items:center;justify-content:center;cursor:pointer;position:relative;transition:background var(--animation-speed) ease,color var(--animation-speed) ease;clip-path:polygon(10% 0,100% 0,100% 90%,90% 100%,0 100%,0 10%)}.button-content{display:flex;align-items:center;justify-content:center;gap:4px;z-index:2}.futuristic-button:hover:not(:disabled){background:var(--hover-bg);color:#fff}.futuristic-button:hover:not(:disabled) .button-content{filter:drop-shadow(0 0 5px var(--glow-color))}.futuristic-button.active{background:var(--active-bg);color:var(--glow-color);box-shadow:inset 0 0 10px var(--active-bg)}.futuristic-button.active .button-content{filter:drop-shadow(0 0 8px var(--glow-color))}.ai-button{background:radial-gradient(circle,rgba(0,255,255,.1) 0%,transparent 70%);border:1px solid var(--glow-color-faded)}.ai-button .button-content{color:var(--glow-color);animation:pulse-glow 3s infinite ease-in-out}.ai-button:hover:not(:disabled){border-color:var(--glow-color);background:radial-gradient(circle,rgba(0,255,255,.2) 0%,transparent 70%)}.ai-button:hover:not(:disabled) .button-content{animation-play-state:paused;color:#fff}.futuristic-dropdown{position:relative}.futuristic-dropdown-menu{position:absolute;top:calc(100% + 8px);left:0;background:var(--bg-color);backdrop-filter:blur(12px) saturate(180%);border:1px solid var(--border-color);border-radius:8px;padding:8px;z-index:1001;width:max-content;min-width:160px;opacity:0;visibility:hidden;transform:translateY(10px);transition:all var(--animation-speed) cubic-bezier(0.16,1,0.3,1)}.futuristic-dropdown-menu.show{opacity:1;visibility:visible;transform:translateY(0)}.futuristic-dropdown-item{background:none;border:none;color:var(--text-color);padding:8px 12px;border-radius:6px;cursor:pointer;display:block;width:100%;text-align:left;transition:background var(--animation-speed) ease,color var(--animation-speed) ease}.futuristic-dropdown-item:hover{background:var(--hover-bg);color:#fff}.chevron-icon{transition:transform var(--animation-speed) ease}.chevron-icon.open{transform:rotate(180deg)}`}</style>);export default FuturisticToolbarStyles;