import { useCallback, useEffect, useRef, useState } from "react";
import PropTypes from "prop-types";
import {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  Code,
  Link,
  Image,
  List,
  ListOrdered,
  Quote,
  AlignLeft,
  AlignCenter,
  AlignRight,
  Indent,
  Outdent,
  Table,
  Columns,
  Eraser,
  Undo,
  Redo,
  Minus,
  FileUp,
  Superscript,
  Subscript,
  Video,
  Sparkles,
  GripVertical,
  Pin,
  PinOff,
} from "lucide-react";
import FuturisticToolbarButton from "./components/FuturisticToolbarButton";
import FuturisticDropdown from "./components/FuturisticDropdown";
import FuturisticColorPicker from "./components/FuturisticColorPicker";
import FuturisticEmojiPicker from "./components/FuturisticEmojiPicker";
import FuturisticToolbarStyles from "../../../../styles/theme/futuristic/FuturisticToolbar.styles";
const FONT_FACES = [
  "Arial",
  "Georgia",
  "Helvetica",
  "Tahoma",
  "Times New Roman",
  "Verdana",
  "Courier New",
];
const HEADINGS = {
  "Heading 1": "<h1>",
  "Heading 2": "<h2>",
  "Heading 3": "<h3>",
  "Heading 4": "<h4>",
  "Heading 5": "<h5>",
  Paragraph: "<p>",
};

const LINE_HEIGHTS = { Single: "1", 1.15: "1.15", 1.5: "1.5", Double: "2" };
const FONT_SIZES = { Small: "2", Normal: "3", Large: "5", Huge: "7" };
const FONT_SIZES_MAP_REVERSE = {
  "13px": "Small",
  "16px": "Normal",
  "24px": "Large",
  "32px": "Huge",
};
const HEADINGS_MAP_REVERSE = { h1: "H1", h2: "H2", h3: "H3", p: "Paragraph" };
const columnTableStyle ="width: 100%; border-collapse: collapse; table-layout: fixed; margin: 0; padding: 0;";
const columnCellStyle = "padding: 0; vertical-align: top;";
const responsiveCss = `<style>@media screen and (max-width: 600px) { .responsive-cell { display: block !important; width: 100% !important; box-sizing: border-box !important; } }</style>`;

const COLUMN_LAYOUTS = {
  "2 Columns (1:1)": `
    ${responsiveCss}
    <table class="responsive-table" style="${columnTableStyle}">
      <tbody>
        <tr>
          <td class="responsive-cell" style="${columnCellStyle} width: 50%;">
            <p>Column 1</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 50%;">
            <p>Column 2</p>
          </td>
        </tr>
      </tbody>
    </table>`,

  "3 Columns (1:1:1)": `
    ${responsiveCss}
    <table class="responsive-table" style="${columnTableStyle}">
      <tbody>
        <tr>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C1</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C2</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C3</p>
          </td>
        </tr>
      </tbody>
    </table>`,

  "2 Columns (1:2)": `
    ${responsiveCss}
    <table class="responsive-table" style="${columnTableStyle}">
      <tbody>
        <tr>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C1</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 66.66%;">
            <p>C2</p>
          </td>
        </tr>
      </tbody>
    </table>`,

  "2 Columns (2:1)": `
    ${responsiveCss}
    <table class="responsive-table" style="${columnTableStyle}">
      <tbody>
        <tr>
          <td class="responsive-cell" style="${columnCellStyle} width: 66.66%;">
            <p>C1</p>
          </td>
          <td class="responsive-cell" style="${columnCellStyle} width: 33.33%;">
            <p>C2</p>
          </td>
        </tr>
      </tbody>
    </table>`,
};

const FuturisticToolbar = ({
  currentStyles,
  applyStyle,
  applyLineHeight,
  activeStyles,
  onOpenLinkModal,
  onImageUpload,
  onDocxUpload,
  onVideoModalOpen,
  onTableModalOpen,
  onAiRefactor,
  content,
  isGenerating,
}) => {
  const imageFileInputRef = useRef(null);
  const docxFileInputRef = useRef(null);
  const toolbarRef = useRef(null);
  const [isDocked, setIsDocked] = useState(true);
  const isDragging = useRef(false);
  const position = useRef({ x: 50, y: 50 });
  const dragOffset = useRef({ x: 0, y: 0 });

  const onDragStart = useCallback(
    (e) => {
      if (e.button !== 0 || isDocked) return;
      isDragging.current = true;
      const toolbarNode = toolbarRef.current;
      if (!toolbarNode) return;

      dragOffset.current = {
        x: e.clientX - position.current.x,
        y: e.clientY - position.current.y,
      };
      toolbarNode.classList.add("is-dragging");
      e.preventDefault();
    },
    [isDocked]
  );

  useEffect(() => {
    const onDragMove = (e) => {
      if (!isDragging.current || isDocked) return;
      const newX = e.clientX - dragOffset.current.x;
      const newY = e.clientY - dragOffset.current.y;
      toolbarRef.current.style.left = `${newX}px`;
      toolbarRef.current.style.top = `${newY}px`;
    };

    const onDragEnd = (e) => {
      if (!isDragging.current) return;
      isDragging.current = false;
      const toolbarNode = toolbarRef.current;
      if (!toolbarNode) return;

      toolbarNode.classList.remove("is-dragging");
      position.current = {
        x: e.clientX - dragOffset.current.x,
        y: e.clientY - dragOffset.current.y,
      };
    };

    window.addEventListener("mousemove", onDragMove);
    window.addEventListener("mouseup", onDragEnd);
    return () => {
      window.removeEventListener("mousemove", onDragMove);
      window.removeEventListener("mouseup", onDragEnd);
    };
  }, [isDocked]);

  const [selectedValues, setSelectedValues] = useState({
    headings: "Paragraph",
    fontFamily: "Arial",
    fontSize: "Normal",
    lineHeight: "Single",
  });

  useEffect(() => {
    if (currentStyles && Object.keys(currentStyles).length > 0) {
      const newSelected = {};
      newSelected.headings =
        HEADINGS_MAP_REVERSE[currentStyles.formatBlock] || "Paragraph";
      newSelected.fontFamily =
        FONT_FACES.find((f) =>
          currentStyles.fontName?.toLowerCase().includes(f.toLowerCase())
        ) || "Arial";
      newSelected.fontSize =
        Object.entries(FONT_SIZES_MAP_REVERSE).find(([px]) =>
          currentStyles.fontSize?.includes(px)
        )?.[1] || "Normal";

      let detectedLineHeightKey = "Single";
      const rawLineHeight = currentStyles.lineHeight;
      const rawFontSize = currentStyles.fontSize;

      if (rawLineHeight && rawLineHeight !== "normal") {
        if (rawLineHeight.endsWith("px") && rawFontSize?.endsWith("px")) {
          const lineHeightPx = parseFloat(rawLineHeight);
          const fontSizePx = parseFloat(rawFontSize);
          if (fontSizePx > 0) {
            const multiplier = (lineHeightPx / fontSizePx).toFixed(2);
            detectedLineHeightKey =
              Object.keys(LINE_HEIGHTS).find(
                (key) => parseFloat(LINE_HEIGHTS[key]).toFixed(2) === multiplier
              ) || detectedLineHeightKey;
          }
        } else {
          detectedLineHeightKey =
            Object.keys(LINE_HEIGHTS).find(
              (key) => LINE_HEIGHTS[key] === rawLineHeight
            ) || detectedLineHeightKey;
        }
      } else if (rawLineHeight === "normal") {
        detectedLineHeightKey = "1.15";
      }
      newSelected.lineHeight = detectedLineHeightKey;
      setSelectedValues((prev) => ({ ...prev, ...newSelected }));
    }
  }, [currentStyles]);

  const handleDropdownChange = (dropdown, value) => {
    setSelectedValues((prev) => ({ ...prev, [dropdown]: value }));
  };
  const handleToggleDock = () => setIsDocked((prev) => !prev);
  return (
    <>
      <FuturisticToolbarStyles />
      <div
        ref={toolbarRef}
        className={`futuristic-toolbar ${isDocked ? "docked" : ""}`}
        style={isDocked? {}: {
                top: `${position.current.y}px`,
                left: `${position.current.x}px`,
              }
        }
      >
        {!isDocked && (
          <div className="drag-handle" onMouseDown={onDragStart} title="Drag Toolbar" ><GripVertical size={20} /></div>
        )}
        <div className="toolbar-content">
          <div className="toolbar-group">
            <FuturisticToolbarButton title={isDocked ? "Undock Toolbar" : "Dock Toolbar to Top"} onClick={handleToggleDock}>
              {isDocked ? <PinOff size={18} /> : <Pin size={18} />}
            </FuturisticToolbarButton>
          </div>
          <div className="toolbar-separator" />
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Undo"
              onClick={() => applyStyle("undo")}
            >
              <Undo size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Redo"
              onClick={() => applyStyle("redo")}
            >
              <Redo size={18} />
            </FuturisticToolbarButton>
          </div>
          <div className="toolbar-separator" />
          <div className="toolbar-group">
            <FuturisticDropdown
              title="Headings"
              selectedValue={selectedValues.headings}
            >
              {Object.entries(HEADINGS).map(([k, v]) => (
                <button key={k} className="futuristic-dropdown-item"
                  onClick={() => {
                    applyStyle("formatBlock", v);
                    handleDropdownChange("headings", k);
                  }}
                >{k}</button>
              ))}
            </FuturisticDropdown>
            <FuturisticDropdown
              title="Font"
              selectedValue={selectedValues.fontFamily}
            >
              {FONT_FACES.map((f) => (
                <button
                  key={f}
                  className="futuristic-dropdown-item"
                  style={{ fontFamily: f }}
                  onClick={() => {
                    applyStyle("fontName", f);
                    handleDropdownChange("fontFamily", f);
                  }}
                >
                  {f}
                </button>
              ))}
            </FuturisticDropdown>
            <FuturisticDropdown
              title="Font Size"
              selectedValue={selectedValues.fontSize}
            >
              {Object.entries(FONT_SIZES).map(([key, value]) => (
                <button
                  key={key}
                  className="futuristic-dropdown-item"
                  onClick={() => {
                    applyStyle("fontSize", value);
                    handleDropdownChange("fontSize", key);
                  }}
                >
                  {key}
                </button>
              ))}
            </FuturisticDropdown>
            <FuturisticDropdown
              title="Line Height"
              selectedValue={selectedValues.lineHeight}
            >
              {Object.entries(LINE_HEIGHTS).map(([k, v]) => (
                <button
                  key={k}
                  className="futuristic-dropdown-item"
                  onClick={() => {
                    applyLineHeight(v);
                    handleDropdownChange("lineHeight", k);
                  }}
                >
                  {k}
                </button>
              ))}
            </FuturisticDropdown>
          </div>
          <div className="toolbar-separator" />
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Bold"
              cmd="bold"
              active={activeStyles}
              onClick={() => applyStyle("bold")}
            >
              <Bold size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Italic"
              cmd="italic"
              active={activeStyles}
              onClick={() => applyStyle("italic")}
            >
              <Italic size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Underline"
              cmd="underline"
              active={activeStyles}
              onClick={() => applyStyle("underline")}
            >
              <Underline size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Strikethrough"
              cmd="strikeThrough"
              active={activeStyles}
              onClick={() => applyStyle("strikeThrough")}
            >
              <Strikethrough size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Superscript"
              cmd="superscript"
              active={activeStyles}
              onClick={() => applyStyle("superscript")}
            >
              <Superscript size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Subscript"
              cmd="subscript"
              active={activeStyles}
              onClick={() => applyStyle("subscript")}
            >
              <Subscript size={18} />
            </FuturisticToolbarButton>
            <FuturisticColorPicker
              command="foreColor"
              applyStyle={applyStyle}
              currentStyles={currentStyles}
            />
            <FuturisticColorPicker
              command="backColor"
              applyStyle={applyStyle}
              currentStyles={currentStyles}
            />
          </div>
          <div className="toolbar-separator" />
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Align Left"
              cmd="justifyLeft"
              active={activeStyles}
              onClick={() => applyStyle("justifyLeft")}
            >
              <AlignLeft size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Align Center"
              cmd="justifyCenter"
              active={activeStyles}
              onClick={() => applyStyle("justifyCenter")}
            >
              <AlignCenter size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Align Right"
              cmd="justifyRight"
              active={activeStyles}
              onClick={() => applyStyle("justifyRight")}
            >
              <AlignRight size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Ordered List"
              cmd="insertOrderedList"
              active={activeStyles}
              onClick={() => applyStyle("insertOrderedList")}
            >
              <ListOrdered size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Unordered List"
              cmd="insertUnorderedList"
              active={activeStyles}
              onClick={() => applyStyle("insertUnorderedList")}
            >
              <List size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Outdent"
              onClick={() => applyStyle("outdent")}
            >
              <Outdent size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Indent"
              onClick={() => applyStyle("indent")}
            >
              <Indent size={18} />
            </FuturisticToolbarButton>
          </div>
          <div className="toolbar-separator" />
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Add/Edit Link"
              onClick={onOpenLinkModal}
            >
              <Link size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton title="Blockquote" onClick={() => applyStyle("formatBlock", "<blockquote>")}> <Quote size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton title="Code Block" onClick={() => applyStyle("formatBlock", "<pre>")}>
              <Code size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton title="Horizontal Line" onClick={() => applyStyle("insertHorizontalRule")}>
              <Minus size={18} />
            </FuturisticToolbarButton>
          </div>
          <div className="toolbar-separator" />
          <div className="toolbar-group">
            <FuturisticToolbarButton title="Insert Image" onClick={() => imageFileInputRef.current?.click()}>
              <Image size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton title="Insert Video" onClick={onVideoModalOpen}>
              <Video size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton title="Insert Table" onClick={onTableModalOpen}>
              <Table size={18} />
            </FuturisticToolbarButton>
            <FuturisticDropdown title="Insert Columns" icon={<Columns size={18} />}>
              {Object.entries(COLUMN_LAYOUTS).map(([k, v]) => (
                <button key={k} className="futuristic-dropdown-item" onClick={() => applyStyle("insertHTML", v + "<p><br></p>")}>{k}</button>
              ))}
            </FuturisticDropdown>
            <FuturisticToolbarButton title="Import from Word" onClick={() => docxFileInputRef.current?.click()}>
              <FileUp size={18} />
            </FuturisticToolbarButton>
            <FuturisticEmojiPicker applyStyle={applyStyle} />
          </div>
          <div className="toolbar-separator" />
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Clear Formatting"
              onClick={() => applyStyle("removeFormat")}
            >
              <Eraser size={18} />
            </FuturisticToolbarButton>
            <div className="toolbar-separator" />
            <FuturisticToolbarButton title="Improve with AI" onClick={onAiRefactor} disabled={!content || isGenerating} className="ai-button rounded-3">
              {isGenerating ? ( <div className="spinner"></div>
              ) : (<Sparkles size={18} /> )}
            </FuturisticToolbarButton>
          </div>
        </div>
      </div>
      <input type="file" accept="image/*" ref={imageFileInputRef} onChange={onImageUpload} style={{ display: "none" }}/>
      <input type="file" accept=".docx" ref={docxFileInputRef} onChange={onDocxUpload} style={{ display: "none" }}/>
    </>
  );
};
FuturisticToolbar.propTypes = { currentStyles: PropTypes.object, applyStyle: PropTypes.func.isRequired, activeStyles: PropTypes.object.isRequired, applyLineHeight: PropTypes.func.isRequired, onOpenLinkModal: PropTypes.func.isRequired, onImageUpload: PropTypes.func.isRequired, onDocxUpload: PropTypes.func.isRequired, onVideoModalOpen: PropTypes.func.isRequired, onTableModalOpen: PropTypes.func.isRequired, onAiRefactor: PropTypes.func.isRequired, content: PropTypes.string, isGenerating: PropTypes.bool.isRequired,};
export default FuturisticToolbar;