import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Palette, Highlighter } from "lucide-react";
import ModernDropdown from "./ModernDropdown";

const COLORS = [
  "#000000",
  "#222222",
  "#444444",
  "#666666",
  "#888888",
  "#999999",
  "#BBBBBB",
  "#CCCCCC",
  "#DDDDDD",
  "#EEEEEE",
  "#FFFFFF",
  "#FF0000",
  "#CC0000",
  "#990000",
  "#660000",
  "#FF6666",
  "#FF9900",
  "#FF6600",
  "#CC6600",
  "#FFB84D",
  "#FFFF00",
  "#FFEE00",
  "#CCCC00",
  "#999900",
  "#00FF00",
  "#00CC00",
  "#009900",
  "#66FF66",
  "#33CC33",
  "#00FFFF",
  "#00CCCC",
  "#009999",
  "#66FFFF",
  "#0000FF",
  "#0000CC",
  "#000099",
  "#6666FF",
  "#3366FF",
  "#9900FF",
  "#6600CC",
  "#9933FF",
  "#CC99FF",
  "#FF00FF",
  "#CC00CC",
  "#FF66FF",
  "#FF99FF",
];

const ModernColorPicker = ({ command, applyStyle, currentStyles }) => {
  const [selectedColor, setSelectedColor] = useState(
    command === "foreColor" ? "#000000" : "#FFFF00"
  );
  const title = command === "foreColor" ? "Text Color" : "Highlight Color";

  useEffect(() => {
    if (currentStyles) {
      const colorValue =
        command === "foreColor"
          ? currentStyles.foreColor
          : currentStyles.backColor;
      if (colorValue) {
        setSelectedColor(colorValue);
      }
    }
  }, [currentStyles, command]);

  const handleColorSelect = (color) => {
    setSelectedColor(color);
    applyStyle(command, color);
  };
  const icon =
    command === "foreColor" ? (
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Palette size={18} />
        <div
          style={{
            width: "16px",
            height: "4px",
            backgroundColor: selectedColor,
            borderRadius: "2px",
            marginTop: "2px",
          }}
        />
      </div>
    ) : (
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Highlighter size={18} />
        <div
          style={{
            width: "16px",
            height: "4px",
            backgroundColor: selectedColor,
            borderRadius: "2px",
            marginTop: "2px",
          }}
        />
      </div>
    );
  // --- END: NEW CODE ---

  return (
    <ModernDropdown title={title} icon={icon}>
      <div className="modern-color-picker">
        {COLORS.map((color) => (
          <button
            key={color}
            type="button"
            className="modern-color-swatch"
            style={{ backgroundColor: color }}
            onClick={() => handleColorSelect(color)} // Use the new handler
            aria-label={color}
          />
        ))}
      </div>
    </ModernDropdown>
  );
};

ModernColorPicker.propTypes = {
  command: PropTypes.oneOf(["foreColor", "backColor"]).isRequired,
  applyStyle: PropTypes.func.isRequired,
  // We now expect the currentStyles from the parent
  currentStyles: PropTypes.object,
};

export default ModernColorPicker;
