import { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";
import { ChevronDown } from "lucide-react";

const FuturisticDropdown = ({ title, icon, children, selectedValue }) => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef(null);
  const menuRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target) &&
        menuRef.current &&
        !menuRef.current.contains(event.target)
      ) {
        setIsOpen(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  return (
    <div className="futuristic-dropdown ps-2" ref={dropdownRef}>
      <button
        type="button"
        title={title}
        onClick={() => setIsOpen(!isOpen)}
        onMouseDown={(e) => e.preventDefault()}
        className="futuristic-button"
        aria-expanded={isOpen}
        style={{ padding: "0 8px", minWidth: selectedValue ? "110px" : "auto" }}
      >
        <span className="button-content">
          {selectedValue ? (
            <span
              style={{
                overflow: "hidden",
                textOverflow: "ellipsis",
                whiteSpace: "nowrap",
                fontSize: "13px",
                textAlign: "left",
                flexGrow: 1,
              }}
            >
              {selectedValue}
            </span>
          ) : (
            icon
          )}
          <ChevronDown
            size={16}
            className={`chevron-icon ${isOpen ? "open" : ""}`}
          />
        </span>
      </button>
      <div
        ref={menuRef}
        className={`futuristic-dropdown-menu ${isOpen ? "show" : ""}`}
        onClick={() => setIsOpen(false)}
      >
        {children}
      </div>
    </div>
  );
};

FuturisticDropdown.propTypes = {
  title: PropTypes.string.isRequired,
  icon: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
  selectedValue: PropTypes.string,
};

export default FuturisticDropdown;
