import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Link, Pilcrow, ExternalLink } from "lucide-react";
import BaseModal from "./BaseModal";

const LinkModal = ({ isOpen, onClose, onApply, initialData, isEditing }) => {
  const [linkData, setLinkData] = useState({ text: "", href: "" });

  useEffect(() => {
    if (isOpen) { setLinkData(initialData);}
  }, [isOpen, initialData]);

  if (!isOpen) return null;
  return (
    <BaseModal onClose={onClose} title={isEditing ? "Edit Link" : "Add Link"} icon={<Link size={20} className="text-primary" />}>
      <div className="modal-body p-4">
        <div className="mb-3">
          <label className="form-label fw-semibold">Link Text</label>
          <div className="input-group">
            <span className="input-group-text"> <Pilcrow size={16} /> </span>
            <input  type="text" className="form-control" value={linkData.text}
              onChange={(e) => setLinkData({ ...linkData, text: e.target.value })}/>
          </div>
        </div>
        <div>
          <label className="form-label fw-semibold">Link URL</label>
          <div className="input-group">
            <span className="input-group-text"> <ExternalLink size={16} /></span>
            <input type="url" className="form-control" value={linkData.href}
              onChange={(e) => setLinkData({ ...linkData, href: e.target.value }) }
              placeholder="https://example.com"
              autoFocus
            />
          </div>
        </div>
      </div>
      <div className="modal-footer border-0 bg-light">
        <button type="button" className="btn editor-secondary" onClick={onClose} >Cancel</button>
        <button type="button" className="btn editor-primary" onClick={() => onApply(linkData)}>Apply Link</button>
      </div>
    </BaseModal>
  );
};

LinkModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onApply: PropTypes.func.isRequired,
  initialData: PropTypes.shape({
    text: PropTypes.string,
    href: PropTypes.string,
  }).isRequired,
  isEditing: PropTypes.bool.isRequired,
};

export default LinkModal;
