import PropTypes from "prop-types";
import ClassicStatusbar from "./theme/classic/ClassicStatusbar";
import FuturisticStatusbar from "./theme/futuristic/FuturisticStatusbar";
import ModernStatusBar from "./theme/modern/ModernStatusBar";

const StatusBar = ({ theme, ...props }) => {
  if (theme === "modern") { return <ModernStatusBar {...props} />;}
  if (theme === "futuristic") {return <FuturisticStatusbar {...props} />;}
  return <ClassicStatusbar {...props} />;
};
StatusBar.propTypes = { theme: PropTypes.oneOf(["classic", "modern", "futuristic"]),};
export default StatusBar;