const ModernStatusBarStyles=()=>(<style>{`.modern-statusbar{display:flex;justify-content:space-between;align-items:center;padding:6px 16px;font-size:13px;transition:background-color .3s ease,border-color .3s ease,color .3s ease;border-top:2px solid #1967d2}.statusbar-segment{display:flex;align-items:center;gap:12px}.statusbar-divider{opacity:.5}.statusbar-button{background:none;border:none;border-radius:6px;padding:6px;cursor:pointer;display:inline-flex;align-items:center;justify-content:center;transition:background-color .2s ease,color .2s ease}.theme-light .modern-statusbar{background-color:#f8f9fa;border-color:#e0e0e0;color:#5f6368}.theme-light .statusbar-button{color:#5f6368}.theme-light .statusbar-button:hover{background-color:#f1f3f4;color:#202124}.theme-dark .modern-statusbar{background-color:#2c2d2f;border-color:#3c4043;color:#bdc1c6}.theme-dark .statusbar-button{color:#bdc1c6}.theme-dark .statusbar-button:hover{background-color:#3c4043;color:#e8eaed}`}</style>);
export default ModernStatusBarStyles;