import React from "react";
import PropTypes from "prop-types";

const StatusbarButton = React.memo(
  ({ onClick, title, command, activeStyles, disabled = false, children }) => (
    <button type="button" onClick={onClick} onMouseDown={(e) => e.preventDefault()} className={`btn btn-outline-secondary ${activeStyles && activeStyles.has(command) ? "active" : ""}`} data-bs-toggle="tooltip" data-bs-placement="bottom" title={title} disabled={disabled}>
      {children}
    </button>
  )
);
StatusbarButton.propTypes = { onClick: PropTypes.func.isRequired, title: PropTypes.string.isRequired, command: PropTypes.string, activeStyles: PropTypes.object, disabled: PropTypes.bool, children: PropTypes.node.isRequired,};
StatusbarButton.displayName = "StatusToolbarButton";
export default StatusbarButton;